import React from 'react';

import { Link } from 'react-router-dom';
import FixedFormatPrice from '../../fixedFormatPrice';

function NoEmptyCart({ cart, orderTotal, removeProduct }) {
    let test = 'ziro_product';
    return (
        <div className="bg_cart">
            <br />
            <div className="res_tab">
                <table>
                    <tbody>
                        <tr>
                            <th>picture</th>
                            <th className="fixed_name_length">name</th>
                            <th>price</th>
                            <th>quantity</th>
                            <th>total price</th>
                        </tr>
                        {cart.map((product, key) => {
                            let a = product.price;
                            let b = product.countOrder;
                            let result = a * b;
                            orderTotal += result;
                            return (<tr key={product.id}>
                                <td><img src={product.picture} alt="" /></td>
                                <td><span>{product.itemOfModel_name}</span></td>
                                <td>${product.price}</td>
                                <td className="quantity_buttons">
                                    {product.countOrder === 0
                                        ? <span className={"product_couter " + test}>{product.countOrder}</span>
                                        : <span className="product_couter">{product.countOrder}</span>
                                    }
                                    <button className="btn_increment" onClick={() => removeProduct(product, 'increment')}>+</button>
                                    <button className="btn_subtract" onClick={() => removeProduct(product, 'subtract')}>-</button>
                                </td>
                                <td>
                                    <div className="last_box_cart">
                                        <span>{FixedFormatPrice.formatCurrency(result)}</span>

                                        <Link className="quantity_remove" to="/cart" onClick={() => removeProduct(product, 'remove')}> <i className="fas fa-trash-alt"></i> </Link>
                                    </div>
                                </td>
                            </tr>)
                        })}
                    </tbody>
                    <div className="order_total">
                        <span>Order total: {FixedFormatPrice.formatCurrency(orderTotal)}</span>
                    </div>
                </table>
            </div>
            <br />
        </div>
    )
}
export default NoEmptyCart;













