import React from 'react';
import { NavLink } from 'react-router-dom';

const ShowCartProducts = (props) => {
    return (
        <li><NavLink to="/cart"
            activeStyle={{ color: '#FFFFFF' }} >My Cart  <i className="fas fa-cart-plus"></i></NavLink> ( <span className="count_products">{props.count}</span> )
            </li>
    )
}
export default ShowCartProducts;
